.columns { 
	display: flex;
	&.is-multiline {
		flex-wrap: wrap;
	}
	&.has--padding{
		margin: -0.75em;
		// margin-left: -1.5em;
		// margin-bottom: -1.5em;
		& > .column {
			padding: 0.75em;
			// padding-left: 1.5em;
			// padding-bottom: 1.5em;
		}
	}
}
.column {
	flex: 1;
	&.is-3 {
		width: 33.33333%;
		flex: none;
	}
	&.is-2 {
		width: 16.66667%;
		flex: none;
	}
	&.is-12 {
		width: 100%;
		flex: none;
	}

}

